<script>

export default {
  name: "Navigation",
  data() {
    return {
      activeIndex: '1',
      activeIndex2: '1',
      navStyle: {
        width: "100%",
        backgroundColor: "white",
        alpha: "0.5"
      },
      langs: ['en', 'cn']
    };
  },
  methods: {
    skip(a) {
      this.$router.push(a);
    },
    docSkip(a){
      a = '/document/'+ a;
      this.$router.push(a);
    }
  }
}
</script>

